

.main-nav {
    position: relative;

}
.main-nav__toggle {
    position: absolute;
    top: -46px;
    right: 19px;
    z-index: 2;
    display: block;
    width: 24px;
    height: 24px;
    background-color:transparent;
    border: none;
    cursor: pointer;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color:$black;
  box-shadow: 0 8px 0 0 $black, 0 16px 0 0 $black;
}
.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 0;
    width: 24px;
    height: 2px;
    background-color: $black;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}
@media (max-width: $mobile-width-only) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
  .main-nav__toggle--nojs {
    display: none;
  }